@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Open Sans",sans-serif;margin:0;padding:0;box-sizing:border-box}.App{display:grid}.header{width:1fr;height:64px;padding-right:16px;padding-left:16px;display:flex;justify-content:space-between;align-items:center;box-shadow:0px 5px 5px #d3d3d3}.header_logoWrapper{display:flex;width:105px;justify-content:space-between}.header_logo{display:flex;flex-direction:column}.header_logoTitle{font-weight:bold;font-size:6px;line-height:7px;color:#000}.header_title{font-weight:bold;font-size:16px;line-height:30px;color:#000}.header_profileInfo{display:flex;justify-content:space-between;align-items:center;width:170px}.header_profileName{font-family:Open Sans;font-style:normal;font-weight:normal;font-size:15px;line-height:19px}.navbar{width:52px;height:calc(100vh - 64px);background:#550dc9;box-shadow:2px 0px 4px rgba(0,0,0,.1);padding-top:24px}.navbar_logoWrapper{width:52px;height:32px;display:flex;justify-content:center;align-items:center;background:#440ba0}
